nav {
   background: rgba(0, 0, 0, 0.3);
   width: max-content;
   display: block;
   padding: 0.7rem 1.7rem   ;
   margin-left: 33rem;
   z-index: 2;
   position: fixed;
   border-radius: 3rem;
   backdrop-filter: blur(15px);
   bottom: 2rem;
   display: flex;
   gap: 0.8rem;



}
nav a {
    background-color: transparent;
    margin: 0.5rem;
    display: flex;
    font-size: 1.2rem;
    color: var(--color-light);

   
}

nav a :hover{
    background: rgba(0, 0, 0, 0.3);

}

nav a.active{
    background: var(--color-primary);
    color: var(--color-bg);
}